import React, { useState, useEffect } from "react";
import { OpenAIClient, AzureKeyCredential } from "@azure/openai";
import Loader from "./Loader";
const OpenAIQuiz = () => {
  const endpoint = "https://r-openai.openai.azure.com/";
  const azureApiKey = "77bed86b013e48989337687648b0b987";
  const [messages, setMessages] = useState([]);
  const [loading, setLoading] = useState(false);
  const [score, setScore] = useState(0);
  const fetchChatCompletions = async (userMessage) => {
    setLoading(true);
    const client = new OpenAIClient(
      endpoint,
      new AzureKeyCredential(azureApiKey)
    );
    const deploymentId = "openai-gpt35t0301";
    const result = await client.getChatCompletions(
      deploymentId,
      messages.concat(userMessage)
    );
    const responseMessage = result.choices[result.choices.length - 1]?.message;
    if (responseMessage) {
      setLoading(false);
      setMessages((prevMessages) => [...prevMessages, responseMessage]);
    }
  };
  const handleInputKeyDown = async (event) => {
    if (event.key === "Enter") {
      const userInput = event.target.value;
      if (userInput.trim() !== "") {
        let val = event.target.value.toLowerCase();
        if(val === 'yes' || val ==='y' || val === 'ys') {
          setScore(score + 1);
        }
        setMessages((prevMessages) => [
          ...prevMessages,
          { role: "user", content: userInput },
        ]);
        event.target.value = "";
        await fetchChatCompletions({ role: "user", content: userInput });
      }
    }
  };
  useEffect(() => {
    setMessages([
      { role: "system", content: "Hello! Let's determine if you have TBI." },
      { role: "assistant", content: "Do you experience frequent headaches?" },
    ]);
  }, []);
  return (
    <div className="chatbot">
      <h1 className="titleScore">OpenAI TBI Assessment <span><small>Score: {score} </small></span></h1>
      
      <div>
        {messages.map((message, index) => (
          <div
            key={index}
            className={message.role === "user" ? "prompt" : "response"}
          >
            {message.role === "user" ? (
              <b>You:&nbsp;</b>
            ) : (
              <b>Assistant:&nbsp;</b>
            )}
            {message.content}
          </div>
        ))}
      </div>
      <div className={loading ? "q-processing" : "q-completed"}>
        <div>{loading && <Loader />}</div>
        <input type="text" onKeyDown={handleInputKeyDown} disabled={loading} />
      </div>
    </div>
  );
};
export default OpenAIQuiz;
