import React, { useState, useEffect, useRef } from "react";
import questions from "./data";
import GraphComponent from "./GraphComponent";
import { Helmet } from "react-helmet";
function OpenAiVideoDemo() {
  const [currentIndex, setCurrentIndex] = useState(0);
  const [chatHistory, setChatHistory] = useState([]);
  const [started, setStarted] = useState(false);
  const [videoStarted, setVideoStarted] = useState(false);
  const [videoEnded, setVideoEnded] = useState(false);
  const [typingAnswer, setTypingAnswer] = useState("");
  const [showGraph, setShowGraph] = useState(false);
  const [isActive, setIsActive] = useState(false);
  const chatRef = useRef(null);

  useEffect(() => {
    setIsActive(false);
    const timer = setTimeout(() => {
      setIsActive(true);
    }, 500); // Duration of the fade-out animation

    return () => {
      clearTimeout(timer);
    };
  }, [currentIndex]);

  const fetchQuestion = () => {
    if (currentIndex >= questions.length) {
      setShowGraph(true);
    } else {
      const currentQuestion =
        questions[currentIndex].text || questions[currentIndex].question;
      setChatHistory((prevHistory) => [
        ...prevHistory,
        { type: "ai", content: currentQuestion },
      ]);
      setVideoEnded(false);
    }
  };

  const smoothScrollTo = (element, target, duration) => {
    const startTime = Date.now();
    const start = element.scrollTop;
    const change = target - start;

    const animateScroll = () => {
      const currentTime = Date.now() - startTime;
      if (currentTime < duration) {
        const t = currentTime / duration;
        element.scrollTop = start + change * t;
        requestAnimationFrame(animateScroll);
      } else {
        element.scrollTop = target;
      }
    };

    animateScroll();
  };
  const typeAnswer = (answer, index = 0) => {
    if (chatRef.current) {
      smoothScrollTo(chatRef.current, chatRef.current.scrollHeight, 200); // 200ms duration
    }

    if (index < answer.length) {
      setTypingAnswer(answer.substring(0, index + 1));
      setTimeout(() => typeAnswer(answer, index + 1), 100);
    } else {
      setChatHistory((prevHistory) => [
        ...prevHistory,
        { type: "user", content: answer },
      ]);
      setTypingAnswer(""); // Clear the typing answer
      setTimeout(() => {
        setCurrentIndex((prevIndex) => prevIndex + 1);
      }, 1000); // 1 second pause
    }
  };

  useEffect(() => {
    if (started) {
      fetchQuestion();
    }
  }, [currentIndex, started]);

  useEffect(() => {
    if (videoEnded) {
      const currentAnswer = questions[currentIndex]?.answer;
      typeAnswer(currentAnswer);
    }
  }, [videoEnded]);

  useEffect(() => {
    if (chatRef.current) {
      smoothScrollTo(chatRef.current, chatRef.current.scrollHeight, 200); // 200ms duration
    }
  }, [chatHistory, videoEnded]);

  return (
    <>
      <Helmet>
        {questions.map((q, index) => (
          <link
            key={index}
            rel="preload"
            href={`/video/${q.video}.mp4`}
            as="video"
            type="video/mp4"
          />
        ))}
      </Helmet>

      {!started ? (
        <div className={`container ${isActive ? "active" : ""}`}>
          <h2>Introduction</h2>
          <video
            width="100%"
            controls
            src={`/video/demo.mp4`}
            type="video/mp4"
          ></video>
          <div className="center space-top">
            <button onClick={() => setStarted(true)} className="get-started">
              Get Started
            </button>
          </div>
        </div>
      ) : showGraph ? (
        <GraphComponent />
      ) : (
        <div className={`container`}>
          <h2>Assessment Demo</h2>
          <div
            className={`container container-transition ${
              !isActive ? "fade-out" : ""
            }`}
          >
            <video
              width="100%"
              autoPlay
              onEnded={() => setVideoEnded(true)}
              onPlay={() => setVideoStarted(true)}
              src={`/video/${questions[currentIndex]?.video}.mp4`}
              type="video/mp4"
              className="ai-video"
              poster="/video/poster.jpeg"
            ></video>
          </div>

          <div
            ref={chatRef}
            style={{
              border: "1px solid #ddd",
              padding: "10px",
              height: "300px",
              overflowY: "scroll",
              marginTop: "20px",
            }}
          >
            {videoStarted &&
              chatHistory.map((chat, index) => (
                <div key={index} style={{ marginBottom: "15px" }}>
                  <div
                    style={{
                      display: "inline-block",
                      padding: "10px",
                      borderRadius: "10px",
                      background: chat.type === "ai" ? "#e6e6e6" : "#4CAF50",
                      color: chat.type === "ai" ? "black" : "white",
                    }}
                  >
                    {chat.content}
                  </div>
                </div>
              ))}
            <div
              style={{
                display: typingAnswer ? "block" : "none",
                marginBottom: "15px",
              }}
            >
              <div
                style={{
                  display: "inline-block",
                  padding: "10px",
                  borderRadius: "10px",
                  background: "#4CAF50",
                  color: "white",
                }}
              >
                {typingAnswer}
              </div>
            </div>
          </div>
        </div>
      )}
    </>
  );
}

export default OpenAiVideoDemo;
