import React, { useState, useEffect } from "react";
function StaticOpenAiR () {
  const [question, setQuestion] = useState("");
  const [answer, setAnswer] = useState("");
  const [chatHistory, setChatHistory] = useState([]);

  const fetchQuestion = () => {
    fetch("http://localhost:8000/getQuestion")
      .then((response) => response.json())
      .then((data) => {
        setQuestion(data.question);
        setChatHistory((prevHistory) => [
          ...prevHistory,
          { type: "ai", content: data.question },
        ]);
      });
  };
  useEffect(() => {
    console.log("Component rendered");
    fetchQuestion();
  }, []);
  const handleSubmit = () => {
    fetch("http://localhost:8000/submitAnswer", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({ answer }),
    })
      .then((response) => response.json())
      .then((data) => {
        setChatHistory((prevHistory) => [
          ...prevHistory,
          { type: "user", content: answer },
        ]);
        fetchQuestion();
        setAnswer("");
      });
  };

  return (
    <div className="container">
      <h2>Staic AI Chat With R</h2>

      <div
        style={{
          border: "1px solid #ddd",
          padding: "10px",
          height: "300px",
          overflowY: "scroll",
        }}
      >
        {chatHistory.map((chat, index) => (
          <div key={index} style={{ marginBottom: "15px" }}>
            <div
              style={{
                display: "inline-block",
                padding: "10px",
                borderRadius: "10px",
                background: chat.type === "ai" ? "#e6e6e6" : "#4CAF50",
                color: chat.type === "ai" ? "black" : "white",
              }}
            >
              {chat.content}
            </div>
          </div>
        ))}
      </div>

      <div style={{ marginTop: "10px" }}>
        <input
          style={{ padding: "10px", width: "80%", fontSize: "16px" }}
          placeholder="Type your answer..."
          value={answer}
          onChange={(e) => setAnswer(e.target.value)}
        />

        <button
          onClick={handleSubmit}
          style={{
            marginLeft: "10px",
            padding: "10px 20px",
            fontSize: "16px",
            background: "#4CAF50",
            color: "white",
            border: 0,
          }}
        >
          Send
        </button>
      </div>
    </div>
  );
}

export default StaticOpenAiR;

